//! Collaps all css: CMD+K CMD+0;
// Variables
@import "./Scss/variables";

//Reset the standart margin and padding for all
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
// Set a main font-size for easyes calculating rem
html {
	font-size: 10px;
}
// Setup the body
body {
	font-family: $font-family;
	background-color: $bg-body-color;
	// background-image: url("https://www.transparenttextures.com/patterns/cardboard.png");
	// background-image: url("https://www.transparenttextures.com/patterns/fresh-snow.png");
	// background-image: url("https://www.transparenttextures.com/patterns/purty-wood.png");
	background-image: url("https://www.transparenttextures.com/patterns/retina-wood.png");
	overflow: hidden;
}
#root {
	display: grid;
	grid-template-columns: minmax(170px, 0.2fr) 1fr;
	min-height: 100vh;
	width: 100%;
	@media (max-width: 991px) {
		display: flex;
		flex-direction: column;
	}
}

// Modificators
.show {
	animation-name: show;
	animation-duration: 0.8s;
	opacity: 1;
	visibility: visible;
	transform: translateY(0px);
	@keyframes show {
		0% {
			opacity: 0;
			visibility: hidden;
			transform: translateY(-20px);
		}
		100% {
			visibility: visible;
			opacity: 1;
			transform: translateY(0px);
		}
	}
}
.hide {
	animation-name: hide;
	animation-duration: 0.2s;
	opacity: 0;
	visibility: hidden;
	@keyframes hide {
		0% {
			visibility: visible;
			opacity: 1;
		}
		100% {
			opacity: 0;
			visibility: hidden;
		}
	}
}
.flx--grow {
	flex-grow: 1;
}
.fullscreen {
	transform: translate3d(-50%, -50%, -150px);
	-webkit-transform: translate3d(-50%, -50%, -150px);
	position: absolute !important;
	height: 100% !important;
	width: 100% !important;
	top: 50%;
	left: 50%;
	z-index: 998;
	box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.2);
}
